body {
  background: #27343E;
  min-height: 100%;
  margin: 0;
  padding: 0;
  color: #e6e6e6;
  font-family: "Open Sans", sans-serif;
  line-height: 1.6em;
}

#app {
  overflow-x: hidden;
}

a {
  color: #e6e6e6;
  text-decoration: none;
}

a.disabled,
fieldset[disabled] a {
  cursor: not-allowed;
  pointer-events: none;
}

img {
  width: 100%;
  border-radius: 4px;
}

#app {
  display: flex;
  min-height: 100vh;
  flex-direction: column;
  justify-content: space-around;
}

#content {
  flex: 1;
  overflow: auto;
}

.movies,
.shows,
.movie,
.movie-edit,
.not-found,
.season,
.settings,
.show,
.show-edit {
  padding: 0px 30px 30px;
  overflow: auto;
}

.results,
.search {
  overflow: auto;
}

#footer {
  color: #43B094;
  background-color: rgba(255, 255, 255, 0.1);
  text-align: center;
  font-weight: 300;
  padding: 15px;
}

h1,
h2,
h3,
h4 {
  font-weight: 300;
}

h1 {
  line-height: 1.0em;
}

.loading-enter-active, .loading-leave-active {
  transition: all 0.3s ease;
}

.loading-enter, .loading-leave-active {
  opacity: 0;
}

#header {
  width: 100%;
  position: relative;
  top: 0;
  left: 0;
  padding: 15px 30px;
  overflow: auto;
  transition: left 0.3s ease;
}

#header #logo {
  text-transform: uppercase;
  font-weight: 800;
  font-size: 24px;
  color: #43B094;
  letter-spacing: -1px;
  padding: 10px 15px;
  float: left;
  line-height: 1em;
}

@media only screen and (min-width: 768px) {
  #header {
    left: 200px;
    width: calc(100% - 200px);
  }
  #header #logo {
    padding: 10px 0px !important;
  }
}

#navigation {
  width: 50%;
  min-height: 100%;
  height: 100%;
  position: fixed;
  top: 0;
  left: -50%;
  background: #1D272F;
  overflow-y: scroll;
  transition: left 0.3s ease;
}

#navigation .button-mobile {
  position: absolute;
  top: calc(50% - 22px);
  right: 0;
  border-radius: 4px 0px 0px 4px;
}

#navigation ul {
  list-style-type: none;
  padding: 0px;
  margin-top: 10px;
}

#navigation ul li {
  position: relative;
}

#navigation ul li a {
  display: block;
  padding: 15px;
  padding-left: 30px;
  color: rgba(230, 230, 230, 0.5);
  transition: background-color 0.5s;
  line-height: 1.6em;
}

#navigation ul li a i {
  float: left;
  padding-right: 10px;
}

#navigation ul li a:hover {
  background-color: rgba(255, 255, 255, 0.05);
}

#navigation ul li.active a, #navigation ul li.router-link-active a {
  color: #e6e6e6;
  padding-left: 26px;
  border-left: 4px solid #43B094;
  background-color: rgba(255, 255, 255, 0.1);
}

#overlay {
  width: 100%;
  height: 100%;
  position: fixed;
  top: 0;
  left: 0;
  background: transparent;
  z-index: -1;
  transition: background 0.3s ease;
}

#overlay:hover {
  cursor: pointer;
}

#content, #footer {
  width: 100%;
  position: relative;
  transition: left 0.3s ease;
  left: 0;
}

@media only screen and (max-width: 767px) {
  .toggle-menu#app {
    overflow-y: hidden;
  }
  .toggle-menu #header,
  .toggle-menu #content,
  .toggle-menu #footer {
    left: 50%;
  }
  .toggle-menu #navigation {
    left: 0;
    z-index: 2;
  }
  .toggle-menu #overlay {
    display: block;
    z-index: 1;
    background: rgba(0, 0, 0, 0.5);
  }
}

@media only screen and (min-width: 768px) {
  #navigation {
    width: 200px;
    left: 0;
  }
  #content {
    left: 200px;
    width: calc(100% - 200px);
    max-width: 1080px;
  }
  #footer {
    left: 200px;
    width: calc(100% - 200px);
  }
}

.grid {
  list-style-type: none;
  padding: 0px;
  overflow: auto;
}

.grid li {
  margin-bottom: 10px;
}

.grid li:nth-child(1n) {
  float: left;
  margin-right: 10px;
}

.grid .button {
  width: 100%;
  margin-right: 0px;
  margin-bottom: 0px;
}

@media only screen and (max-width: 767px) {
  .grid li {
    width: calc(100% * 1 / 2 - 5px);
  }
  .grid li:nth-child(2n+1) {
    clear: left;
  }
  .grid li:nth-child(2n) {
    margin-right: 0px !important;
  }
}

@media only screen and (min-width: 768px) and (max-width: 1023px) {
  .grid li {
    width: calc(100% * 1 / 3 - 6.667px);
  }
  .grid li:nth-child(3n+1) {
    clear: left;
  }
  .grid li:nth-child(3n) {
    margin-right: 0px !important;
  }
}

@media only screen and (min-width: 1024px) {
  .grid li {
    width: calc(100% * 1 / 4 - 7.5px);
  }
  .grid li:nth-child(4n+1) {
    clear: left;
  }
  .grid li:nth-child(4n) {
    margin-right: 0px !important;
  }
}

.cards {
  clear: both;
}

.cards .card {
  margin-bottom: 20px;
}

.cards .card img {
  overflow: hidden;
  border-radius: 4px;
  width: 100%;
}

.cards .card span {
  display: block;
  padding: 0px 8px;
  max-width: 100%;
  white-space: nowrap;
  overflow: hidden;
  text-overflow: ellipsis;
  font-size: 0.8em;
}

.cards .card:nth-child(1n) {
  float: left;
  margin-right: 30px;
}

@media only screen and (max-width: 767px) {
  .card {
    width: calc(100% * 1 / 2 - 15px);
  }
  .card:nth-child(2n+1) {
    clear: left;
  }
  .card:nth-child(2n) {
    margin-right: 0px !important;
  }
}

@media only screen and (min-width: 768px) and (max-width: 1023px) {
  .card {
    width: calc(100% * 1 / 3 - 20px);
  }
  .card span {
    font-size: 0.95em !important;
  }
  .card:nth-child(3n+1) {
    clear: left;
  }
  .card:nth-child(3n) {
    margin-right: 0px !important;
  }
}

@media only screen and (min-width: 1024px) {
  .card {
    width: calc(100% * 1 / 4 - 22.5px);
  }
  .card:nth-child(4n+1) {
    clear: left;
  }
  .card:nth-child(4n) {
    margin-right: 0px !important;
  }
}

