.toast {
  display: table;
  position: fixed;
  min-height: 44px;
  min-width: 288px;
  max-width: 600px;
  padding: 10px 15px;
  box-sizing: border-box;
  box-shadow: 0 2px 5px 0 rgba(0, 0, 0, 0.26);
  border-radius: 4px;
  left: 0;
  bottom: 0;
  margin: 15px 30px;
  cursor: default;
  transition: visibility 0.3s, transform 0.3s, opacity 0.3s;
  visibility: hidden;
  opacity: 0;
  transform: translateY(100px);
  -ms-transform: translateY(100px);
  z-index: 9999;
}

.toast.active {
  visibility: visible;
  opacity: 1;
  transform: translateY(0px);
  -ms-transform: translateY(0px);
}

.toast.top {
  top: 0;
  bottom: auto;
  transform: translateY(-100px);
  -ms-transform: translateY(-100px);
}

.toast.top.active {
  transform: translateY(0px);
  -ms-transform: translateY(0px);
}

.toast.right {
  left: auto;
  right: 0;
}

.toast .progress-bar {
  position: absolute;
  left: 0;
  bottom: 0;
  height: 0.4em;
  background: rgba(255, 255, 255, 0.3);
  width: 0;
  transition: width 3s;
}

.toast .progress-bar.active {
  width: 100%;
}

.toast .message, .toast .action {
  vertical-align: middle;
  display: table-cell;
}

.toast .action {
  float: right;
  width: 24px;
  height: 24px;
}

.toast .action a:hover {
  cursor: pointer;
}

.toast {
  background-color: #818a91;
  color: #fff;
}

.toast.toast-info {
  background-color: #43ABB0;
  color: #fff;
}

.toast.toast-success {
  background-color: #43B094;
  color: #fff;
}

.toast.toast-warning {
  background-color: #F4815F;
  color: #fff;
}

.toast.toast-danger {
  background-color: #D4485B;
  color: #fff;
}

.toast.toast-dark {
  background-color: #000;
  color: #fff;
}

.toast.toast-light {
  background-color: #fff;
  color: #000;
}

.lt-ie10 .progress-bar, .ie9 .progress-bar, .oldie .progress-bar, .no-csstransitions .progress-bar {
  display: none;
}
